        </div>
        <!-- <div class="child3">
          <img class="son1" src="../assets/img1.png" alt="" />
          <div class="son2">{{(allcordsprice/this.bilv).toFixed(4)}} <span>USD</span></div>
        </div> -->
        <div class="child3">
          <img class="son1" src="../assets/img2.png" alt="" />
          <div class="son2">{{ pre2 }} <span>USDT</span></div>
        </div>
      </div>
