          { img: zy1, img1: zy11, title: "head.cc9" },
          { img: zy2, img1: zy22, title: "head.cc10" },
          { img: zy3, img1: zy33, title: "head.cc11" },
          { img: zy4, img1: zy44, title: "head.cc12" },
        ],
      ],
