      />
      <!-- <img src="../../assets/ntf_box/manghe-open.png" v-else class="open" /> -->
      <div class="child2" v-else>
        <div
          v-for="(item, index) in cords"
          :key="index"
          class="open son1"
          v-show="item != 0"
